import { useState, useEffect } from 'react'
import { supabase } from '../utils/supabase'
import { useTranslation } from '../utils/translations'

export const useAuth = (language = 'no') => {
  const [user, setUser] = useState(null)
  const [loading, setLoading] = useState(true)
  const { t } = useTranslation(language)

  useEffect(() => {
    // Pobierz aktualnego uytkownika
    const getUser = async () => {
      try {
        const { data: { user } } = await supabase.auth.getUser()
        setUser(user)
        setLoading(false)
      } catch (error) {
        console.error('Auth error:', error)
        setUser(null)
        setLoading(false)
      }
    }

    getUser()

    // Nasuchuj zmian autoryzacji
    const { data: { subscription } } = supabase.auth.onAuthStateChange(
      (event, session) => {
        setUser(session?.user ?? null)
        setLoading(false)
      }
    )

    return () => subscription.unsubscribe()
  }, [])

  const signIn = async (email, password) => {
    const { data, error } = await supabase.auth.signInWithPassword({
      email,
      password
    })
    
    if (error && error.message === 'Invalid login credentials') {
      return { data, error: { ...error, message: t('invalidLoginCredentials') } }
    }
    
    return { data, error }
  }

  const checkCompanyStatus = async (email) => {
    try {
      const { data: company, error } = await supabase
        .from('companies')
        .select('status, role')
        .eq('admin_email', email)
        .single()
      
      // Jeli nie ma firmy w tabeli, pozwl dostp (fallback dla starych kont)
      if (error || !company) {
        console.log('No company found for email:', email, '- allowing access (fallback)')
        return true
      }
      
      // Pozwl super_admin
      if (company.role === 'super_admin') {
        return true
      }
      
      // Pozwl tylko approved
      if (company.status === 'approved') {
        return true
      }
      
      // Zablokuj pending i rejected
      console.log('Company status:', company.status, 'for email:', email)
      return false
    } catch (error) {
      console.error('Error checking company status:', error)
      // W przypadku bdu, pozwl dostp (fallback)
      return true
    }
  }

  const signUp = async (email, password, companyData) => {
    try {
      // Najpierw zarejestruj uytkownika
      const { data: authData, error: authError } = await supabase.auth.signUp({
        email,
        password
      })
      
      if (authError) {
        console.error('Auth error:', authError)
        return { data: null, error: authError }
      }
      
      console.log('User created:', authData.user?.id)
      
      // Potem utwrz firm z statusem 'pending'
      const { data: companyData2, error: companyError } = await supabase
        .from('companies')
        .insert([{
          name: companyData.name,
          address: companyData.address,
          phone: companyData.phone,
          admin_email: email,
          status: 'pending',
          role: 'admin'
        }])
        .select()
      
      if (companyError) {
        console.error('Company creation error:', companyError)
        // Nie prbuj usuwa uytkownika przez admin API (403 error)
        // Supabase automatycznie wyloguje uytkownika
        return { data: null, error: companyError }
      }
      
      console.log('Company created:', companyData2)
      
      // Natychmiast wyloguj uytkownika po rejestracji
      await supabase.auth.signOut()
      
      return { data: { user: null, session: null }, error: null }
    } catch (error) {
      console.error('SignUp error:', error)
      return { data: null, error }
    }
  }

  const signOut = async () => {
    const { error } = await supabase.auth.signOut()
    return { error }
  }

  return {
    user,
    loading,
    signIn,
    signUp,
    signOut
  }
}